import {
  AbstractAgent as AGUIAbstractAgent,
  AgentConfig as AGUIAgentConfig,
} from "@ag-ui/client";

export interface AgentConfig extends AGUIAgentConfig {
  name?: string;
}

export abstract class AbstractAgent extends AGUIAbstractAgent {
  name: string;

  constructor(config: AgentConfig) {
    super(config);
    this.name = config.name || "";
  }
}
